/** @type {import('../types.d.ts').LanguageProto<'ini'>} */
export default {
	id: 'ini',
	grammar: {
		/**
		 * The component mimics the behavior of the Win32 API parser.
		 *
		 * @see {@link https://github.com/PrismJS/prism/issues/2775#issuecomment-787477723}
		 */

		'comment': {
			pattern: /(^[ \f\t\v]*)[#;][^\n\r]*/m,
			lookbehind: true,
		},
		'section': {
			pattern: /(^[ \f\t\v]*)\[[^\n\r\]]*\]?/m,
			lookbehind: true,
			inside: {
				'section-name': {
					pattern: /(^\[[ \f\t\v]*)[^ \f\t\v\]]+(?:[ \f\t\v]+[^ \f\t\v\]]+)*/,
					lookbehind: true,
					alias: 'selector',
				},
				'punctuation': /\[|\]/,
			},
		},
		'key': {
			pattern: /(^[ \f\t\v]*)[^ \f\n\r\t\v=]+(?:[ \f\t\v]+[^ \f\n\r\t\v=]+)*(?=[ \f\t\v]*=)/m,
			lookbehind: true,
			alias: 'attr-name',
		},
		'value': {
			pattern: /(=[ \f\t\v]*)[^ \f\n\r\t\v]+(?:[ \f\t\v]+[^ \f\n\r\t\v]+)*/,
			lookbehind: true,
			alias: 'attr-value',
			inside: {
				'inner-value': {
					pattern: /^("|').+(?=\1$)/,
					lookbehind: true,
				},
			},
		},
		'punctuation': /=/,
	},
};
